import React from "react";
class MembershipsTable extends React.Component {
	constructor(props) {
		super(props);
		this.state = { type: "single-term" };
		this.handleAnswer = this.handleAnswer.bind(this);
	}

	handleAnswer() {
		if (this.state.type === "full-year") {
			this.setState(() => ({ type: "single-term" }));
		} else {
			this.setState(() => ({ type: "full-year" }));
		}
	}

	displayTable = (type) => {
		const buy = (
			<div className="purchase-online">
				<a href="https://www.eventbrite.ca/e/sparq-studios-membership-registration-winter-2020-tickets-86008406459">
					Purchase Online
				</a>
			</div>
		);

		const MemTable = () => {
			return (
				<React.Fragment>
					<table className="membership-table">
						<thead>
							<tr>
								<th>Number of Individuals</th>
								<th>Discount</th>
								<th>Cost</th>
								<th>Payment Method</th>
							</tr>
						</thead>
						<tbody>
							<tr>
								<td>Single</td>
								<td>0</td>
								<td>{type === "single-term" ? "$30" : "$50"}</td>
								<td>{buy}</td>
							</tr>
							<tr>
								<td>Double(2+)</td>
								<td>{type === "single-term" ? 17 : 20}%</td>
								<td>{type === "single-term" ? "$25" : "$40"}/person</td>
								<td>{buy}</td>
							</tr>
							<tr>
								<td>Team(5+)</td>
								<td>{type === "single-term" ? 33 : 40}%</td>
								<td>{type === "single-term" ? "$20" : "$30"}/person</td>
								<td>{buy}</td>
							</tr>
							<tr>
								<td>Class(10+)</td>
								<td style={{ color: "#004C6D" }} colSpan="3">
									Inquire at{" "}
									<a
										style={{ color: "#004C6D" }}
										href="mailto:sparq.studios@queensu.ca"
									>
										sparq.studios@queensu.ca
									</a>
									.
								</td>
							</tr>
						</tbody>
					</table>
				</React.Fragment>
			);
		};

		return <MemTable />;
	};

	render() {
		return (
			<div>
				<div className="table-title">
					<label>Membership Types </label>
					<select
						className="membership-type"
						onChange={() => this.handleAnswer()}
					>
						<option value="single-term">Single Term</option>
						<option value="full-year">Full Year</option>
					</select>
				</div>
				{this.displayTable(this.state.type)}
			</div>
		);
	}
}

export default MembershipsTable;
