import React from "react";
import "./Footer.css";
import Copyright from ".././media/icons/CopyrightSparQ.svg";

import Instagram from ".././media/icons/Instagram.svg";
import Facebook from ".././media/icons/Facebook.svg";
import SparQ from ".././media/icons/SparQ.svg";

class Footing extends React.Component {
	render() {
		return (
			<div className="footer">
				<img className="sparQ-logo" src={SparQ} alt="SparQ Logo"></img>

				<div className="media-links">
					<a href="https://www.facebook.com/SparQStudios/" className="media">
						<img className="facebook" src={Facebook} alt="facebook link"></img>
					</a>
					<a href="https://www.instagram.com/sparq.studios/" className="media">
						<img
							className="instagram"
							src={Instagram}
							alt="instagram link"
						></img>
					</a>
					<img
						className="copyright"
						src={Copyright}
						alt="Copyright SparQ"
					></img>
				</div>
			</div>
		);
	}
}

export default Footing;
