import React from "react";
import "./Contact.css";
import Email from "./Email";

class Contact extends React.Component {
	render() {
		return (
			<div id="ContactUs">
				<div id="TopHeader">
					<div id="FrontText">
						<h2 className="contact-title">
							Contact <strong>SparQ</strong>
						</h2>
						<hr className="contact-hr"></hr>
					</div>
				</div>
				<div id="container">
					<Email
						title={"Email Us"}
						subtitle={`Questions? Send us a message below or reach out to us on any of our social media platforms! sparq.studios@queensu.ca`}
					/>
					<div id="GoogleMap">
						<h3>Locate Us</h3>
						<hr id="hr1" />
						<p>Room 101 in Mitchell Hall</p>

						<iframe
							title="SparQ Google Map Location"
							src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2858.9713700356265!2d-76.49639428415902!3d44.228247823072515!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x4cd2ab04761a3893%3A0x99afa5fe85c9ace3!2s69%20Union%20St%2C%20Kingston%2C%20ON%20K7L%202N9!5e0!3m2!1sen!2sca!4v1585497425717!5m2!1sen!2sca"
							width="100%"
							height="400vh"
							frameBorder="0"
							style={{ border: 0 }}
							allowFullScreen=""
						></iframe>
						<ul className="locateUs">
							<li>69 Union St. W</li>
							<li>Kingston,ON</li>
							<li>K2L 2N9</li>
						</ul>
					</div>
				</div>
			</div>
		);
	}
}

export default Contact;
