import React from "react";
import "./Email.css";
import Send from ".././media/icons/Send.svg";

class Email extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			subject: "",
			message: "",
		};
	}
	handleSubmit = (event) => {
		event.preventDefault();
		const data = this.state;
		data.message = encodeURIComponent(data.message);
		const mailToMessage =
			"mailto:sparQ.studio@queensu.ca?subject=" +
			data.subject +
			"&body=" +
			data.message;
		window.location.href = mailToMessage;
	};

	handleInputChange = (event) => {
		event.preventDefault();

		this.setState({
			[event.target.name]: event.target.value,
		});
	};

	render() {
		return (
			<div id="EmailForm">
				<h3>{this.props.title}</h3>
				<hr id="hr2"></hr>
				<p>{this.props.subtitle}</p>
				<form onSubmit={this.handleSubmit}>
					<p>
						<input
							type="text"
							placeholder="  Subject"
							name="subject"
							onChange={this.handleInputChange}
						/>
					</p>
					<p>
						<textarea
							name="message"
							placeholder="  Message"
							cols="30"
							rows="10"
							onChange={this.handleInputChange}
						></textarea>
					</p>
					<p>
						<input id="button" type="image" src={Send} alt="Send Button" />
					</p>
				</form>
			</div>
		);
	}
}
export default Email;
