import React from "react";
import "./Memberships.css";
import Equipment from "./Equipment";
import MembershipsTable from "./MembershipsTable";
import Image1 from ".././media/IMG_8854.jpg";
class Memberships extends React.Component {
	render() {
		return (
			<div className="membership-page">
				<div className="membership-title">
					<h1>
						<strong>SparQ</strong> Memberships
					</h1>
					<hr></hr>
					<h2>
						Our flexible memberships options were made to best suit the size of
						your team and your team's needs. Check out the equipment made
						available to members below!
					</h2>
					<div className="button">
						<a href="https://www.eventbrite.ca/e/sparq-studios-membership-registration-winter-2020-tickets-86008406459">
							Purchase
						</a>
					</div>
				</div>
				<div className="container">
					<div className="membership">
						<MembershipsTable />

						<h2>Training for New Members</h2>
						<p>
							Safety is a top priority in SparQ Studios. All new members must
							complete general safety training before being permitted to work in
							the space. New members must also complete machine-specific
							training before using certain machines. Please
							<a
								className="info"
								href="https://www.queensu.ca/innovationcentre/sparq-studios-makerspace/members"
							>
								{" "}
								review SparQ's policies for members
							</a>{" "}
							for more information.
						</p>
					</div>
					<div className="equipment">
						<div className="equipment-container">
							<h2>Equipment</h2>
							<hr></hr>
							<p className="equipment-title">
								We have a wide variety of tools and resources available at the
								studio to meet your needs. If you would like to see an addition
								to the Studio please contact us at{" "}
								<a href="mailto:sparq.studios@queensu.ca">
									sparq.studios@queensu.ca
								</a>
								!
							</p>
							<Equipment />
						</div>
						<div className="equipment-pictures">
							<img src={Image1} alt=""></img>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Memberships;
