import React from "react";
import "./About.css";
import Dunin from ".././media/Dunin.jpg";
import ScrollIntoView from "react-scroll-into-view";
class About extends React.Component {
	render() {
		return (
			<div>
				<div className="about-title">
					<h1>
						About <strong>SparQ</strong>
					</h1>
					<hr></hr>

					<div className="subtitle" style={{ color: "white" }}>
						Proudly founded by students.
					</div>

					<ScrollIntoView selector=".about-content">
						<div className="button">Tell Me More</div>
					</ScrollIntoView>
				</div>

				<div className="about-content">
					<div className="story">
						<img
							className="dunin"
							src={Dunin}
							alt="Dunin-Deshpande Queen's Innovation Centre"
						></img>
						<h2>Our Story</h2>

						<hr></hr>
						<p>
							With support from the Dunin-Deshpande Queen's Innovation Centre
							(DDQIC), SparQ Studios was founded in 2013 by four students in
							DDQIC's startup incubator who needed modern maker tools to build
							their product ideas. SparQ's first home was a small study room in
							Beamish Munro Hall and it was one of Canada's first on-campus
							makerspaces.
						</p>
					</div>
					<div className="content-container">
						<div className="workshop">
							<h2>Workshops</h2>
							<hr></hr>
							<p>
								Workshops will offer training for beginners on maker techniques
								including sewing, 3D printing, laser cutting/engraving, and
								woodworking. There will also be drop-in project support
								sessions. Follow our{" "}
								<a href="https://www.facebook.com/SparQStudios/">
									Facebook page
								</a>{" "}
								for events and workshop details.
							</p>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default About;
