import React from "react";
import "./Home.css";
import workshop from ".././media/workshop.jpg";
import Email from "./Email";

class Home extends React.Component {
	render() {
		return (
			<div className="container">
				<div className="video">
					<div className="vidText">
						<div className="title">
							<b>SparQ</b> Studios
						</div>
						<hr className="hr"></hr>
						<div className="subtitle">
							SparQ Studios is a makerspace and design studio that provides a
							wide range of tools, machinery, knowledge, and expertise so that
							you can bring your idea into a physical form.
						</div>
						<a href="/about" className="button">
							Learn More
						</a>
					</div>
					<video className="bgVideo" autoPlay muted loop>
						<source src="./Winterwood Video.mp4" type="video/mp4" />
					</video>
				</div>

				<div className="content">
					<div className="home-heading">
						<h1 className="title sub">Maker Space</h1>
						<hr className="hr2"></hr>
						<p className="subtitle2">
							Today, SparQ Studios is located in its own facility on the ground
							floor of Mitchell Hall. SparQ Studios is open to anyone in the
							community who wants to familiarize themselves with modern making
							equipment, learn new skills, and complete their own creative
							projects. It also caters to DDQIC start-ups who need to quickly
							prototype their ideas, a critical stage in the journey to building
							a successful product.
						</p>
					</div>

					<img className="imgCell" src={workshop} alt=""></img>
				</div>

				<div className="content two">
					<Email
						title="Tours"
						subtitle={`Are you interested in scheduling a tour of the facility? Email us, including the number of individuals attending the tour & preferred dates.`}
					></Email>
					<div className="home-heading">
						<h1 className="title sub">Hours of Operation</h1>
						<hr className="hr2"></hr>
						<p className="subtitle2">
							Monday - Friday <br></br> 12:30PM - 6:30PM{" "}
						</p>
						<p className="subtitle2">Closed Weekends</p>
					</div>
				</div>

				<div className="banner">
					<div className="bannerText">
						Learn more about our different membership options available!
					</div>
					<a href="/memberships" className="button">
						Become a member
					</a>
				</div>
			</div>
		);
	}
}

export default Home;
