import React from "react";
import "./NavBar.css";
import { NavLink, withRouter } from "react-router-dom";
class NavBar extends React.Component {
	constructor(props) {
		super(props);
		this.state = { isToggleOn: false };
		this.handleToggle = this.handleToggle.bind(this);
	}
	handleToggle() {
		const { isToggleOn } = this.state;
		this.setState({ isToggleOn: !isToggleOn });
	}

	render() {
		const page = this.props.location.pathname.replace("/", "");
		const dot = <div className={`dot dot-${page}`}></div>;

		return (
			<React.Fragment>
				<span className="navbar-toggle" onClick={this.handleToggle}>
					{dot}
					{dot}
					{dot}
				</span>
				<nav className="Nav">
					<div
						className={`pages ${
							this.state.isToggleOn ? "activeContainer" : ""
						}`}
					>
						<ul className={`navitems ${this.state.isToggleOn ? "active" : ""}`}>
							<NavLink
								className={`link-item ${page}`}
								to="/home"
								activeClassName="link-item-active"
							>
								HOME
							</NavLink>
							<NavLink
								className={`link-item ${page}`}
								to="/about"
								activeClassName="link-item-active"
							>
								ABOUT
							</NavLink>

							<NavLink
								className={`link-item ${page}`}
								to="/memberships"
								activeClassName="link-item-active"
							>
								MEMBERSHIPS
							</NavLink>
							<NavLink
								className={`link-item ${page}`}
								to="/contact"
								activeClassName="link-item-active"
							>
								CONTACT US
							</NavLink>
						</ul>
					</div>
				</nav>
			</React.Fragment>
		);
	}
}

export default withRouter(NavBar);
